@import '~node_modules/modern-normalize/modern-normalize.css';
@import 'src/sass/utils/_vars.scss';
@import 'src/sass/utils/_keyframes.scss';

.about-section {
display: flex;
flex-direction: column;
align-items: center;
padding-left: 20px;
padding-right: 20px;
} 

.name-wrapper{
  width:200px;
  gap: 15px;
  display: flex;
  flex-direction: column;
}
 
@media screen and (min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi),
  screen and (min-resolution: 2dppx) {
  .about__content {
    background-image: url(../images/sectionbg1ph.png);
  }
}

@media screen and (min-width: 1200px) and (min-device-pixel-ratio: 2),
  screen and (min-width: 1200px) and (min-resolution: 192dpi),
  screen and (min-width: 1200px) and (min-resolution: 2dppx) {
    .about__main {
      background-image: url(../images/sectionbg.png);
    }
}

.about {
  background-color: $secondBackgroundColor;
  padding-top: 60px;
  padding-left: 20px;
  &__label {
    font-family: $secondFamily;
    text-align: center;
    text-transform: uppercase;
    color: $secondaryColor;

    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
  }
  &__title {
    font-family: $secondFamily;
    text-align: center;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 34px;
    line-height: 39px;
    color: $primaryColor;
  }
    &__picture {
    display: block;
    margin-bottom: 40px;
  }

  &__img {
    margin-right: 0px;
  }

  &__content {
       font-weight: 500;
    font-size: 12px;
    max-width: 220px;
    line-height: 162%;
    background-image: url(../images/sectionbg1ph.png);
  background-repeat: no-repeat;
  background-position: left;
  }

  &__content-text {
    color: $blackColor;
    margin-bottom: 20px;
    letter-spacing: normal;
    width: 100%;
  }

  &__content-additional {
    color: $mainBrownColor;
    margin-bottom: 20px;
    letter-spacing: normal;

    & span {
      display: block;
      margin-top: 20px;
    }
  }
  &__content-button {
    width: 176px;
    height: 44px;
    text-align: center;
    color: $primaryColor;
    background-color: $whiteColor;
    justify-content: center;
    align-items: center;
    margin-right: 0px;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
    display: flex;
    border-radius: 22px;
    border: transparent;

    &:hover,
    &:focus {
      background-color: #ecebeb;
      cursor: pointer;
    }
  }
  &__content-icon {
    width: 9px;
    height: 9px;
    fill: none;
    stroke: $primaryColor;
    stroke-width: 2px;
    justify-content: center;
    margin-left: 12px;
  }
}

@media screen and (min-width: 768px) {
  .about {
    display: block;
    justify-content: center;
    padding-left: 32px;
    &__label {
      font-weight: 400;
      font-size: 26px;
      line-height: 30px;
    }
    &__title {
      font-weight: 400;
      font-size: 48px;
      line-height: 55px;
      padding-bottom: 40px;
      padding-top: 16px;
    }
    &__main {
      display: flex;
      background-color: #fff5f6;
      
    }

    &__picture {
      margin-bottom: 0;
      margin-right: 32px;
    }

    &__img {
      width: 336px;
      height: 336px;
    }

    &__content {
       font-weight: 500;
      font-size: 14px;
      list-style: 25px;
      max-width: 334px;
    }

    &__content-text {
      margin-bottom: 20px;
    }

    &__content-additional {
      letter-spacing: 0.04em;
      & span {
        display: block;
        margin-top: 20px;
      }
    }
  }
}

@media screen and (min-width: 1280px) {
  
  .milk-shake:hover {
    animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  }
  .about {
    padding-left: 0px;
    &__main {
      padding-left: 16px;
      padding-bottom: 20px;
      display: flex;
      background-image: url(../images/sectionbg.png);
      background-repeat: no-repeat;
    }

    &__picture {
      margin-right: 0 auto;
      padding-right: 99px;
    }

    &__img {
      width: 501px;
      height: 501px;
    }

    &__content {
      font-weight: 500;
      font-size: 16px;
      list-style: 28px;
      max-width: 463px;
      margin-right: 121px;
      padding-top: 90px;
      background-image: none;
    }

    &__content-text {
      margin-bottom: 32px;
      letter-spacing: 0.04em;
    }

    &__content-additional {
      letter-spacing: 0.04em;
      margin-bottom: 40px;
      font-size: 14px;

      & span {
        display: block;
        margin-top: 32px;
      }
    }
  }
}
