@import 'src/sass/utils/_vars.scss';
.contacts {
  padding-top: 60px;
  background-image: linear-gradient($whiteColor 20%, #fff5f6 20%);
}
.contacts-container {
  padding-left: 20px;
  padding-right: 20px;
  justify-content: center;
    display: flex;
    width: auto;
}

.contacts-list {
   display: flex;
 
  
  &__item-product {
    color: $whiteColor;
  }
  &__item-foodtruck {
    background: #c2e297;
    border-radius: 6px;
    padding: 2px 10px;
    width: 75px;
    text-align: center;
     margin-bottom: 20px;
     width: 138px;
  }
  &__item-cafe {
    background: #f0d1a5;
    border-radius: 6px;
    margin-bottom: 20px;
    padding: 2px 10px;
    width: 75px;
    text-align: center;
  }
  &__item {
    display: flex;
    flex-direction: column;.contacts-list-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: row;
      padding-top: 60px;
    }
    justify-content: space-around;
    background: $whiteColor;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
    padding: 40px 32px;
  }
  &__item-text {
    color: #907e82;
    padding-bottom: 24px;
    border-bottom: 1px solid $fourthColor;
  }
  &__item-town {
    color: $blackColor;
    margin-bottom: 8px;
    font-weight: 500;
    font-size: 16px;
    line-height: 186%;
  }
  &__item-program-list {
    margin-bottom: 24px;
    padding-bottom: 24px;
    border-bottom: 1px solid $fourthColor;
  }
  &__item-location {
    margin-bottom: 24px;
  }
  &__item-program-time {
    color: $secondaryColor;
    margin-bottom: 8px;
  }
  &__item-program-day {
    font-weight: 500;
    font-size: 16px;
    line-height: 186%;
    letter-spacing: 0.04em;
    color: $blackColor;
  }
}

 .contact-list__item-link {
  text-decoration: none;
   color: #907e82;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    color: $primaryColor;
  }
}
  .contacts-list {
    flex-direction: column;
    gap: 20px;
  }
  .contacts-list__item {
    width: 280px;
    
  }
   .footer__copyright-text {
    width: 150px;
  }
//button

.contacts-list-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 60px;
}

.contacts-list-btn__item {
  margin-bottom: 10px;
}

.contacts-list-btn__item-locations {
  background: $primaryColor;
  border-radius: 22px;
  font-size: 16px;
  font-weight: 700;
  padding: 12px 16px;
  color: $whiteColor;
  &:hover,
  &:focus {
    cursor: pointer;
  }
 }
 

.contacts-list-btn__item-franchise {
  background: $whiteColor;
  border-radius: 22px;
  font-size: 16px;
  font-weight: 700;
  color: $primaryColor;
  padding: 10px 35px;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    background-color: #ecebeb;
    cursor: pointer;
  }
}

.contacts-list-btn__franchise-icon,
.contacts-list-btn__locations-icon {
  width: 8px;
  height: 10px;
  margin-left: 8px;
  align-items: center;
}

@media screen and (min-width: 768px) {
  .contacts-list {
    padding-left: 16px;
    padding-right: 16px;
    flex-direction: row;
    justify-content: space-between;
    flex-direction: row;
    padding-bottom: 40px;
}
.contacts-list-btn__item-locations {
    padding: 10px 35px;}

    .contacts-list-btn__item-franchise {
      padding: 12px 32px;}

  .contacts-list__item {
    width: 224px;
    height: 528px;
  }

  .contacts-list-btn {
    gap: 15px;
    flex-direction: row;
  }
  
}

@media screen and (min-width: 1280px) {
   
  .contacts-list-btn__item-locations {
        padding: 10px 35px;}


.contacts-list-btn__item-franchise {
    padding: 10px 32px;}
  
  .contacts-list {
    padding-left: 16px;
    padding-right: 16px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .contacts-list__item {
    width: 368px;
    height: 538px;
  }
  .contacts-list-btn {
   flex-direction: row;
   gap: 12px;
  }
  
}
