@import 'src/sass/utils/_vars.scss';
 



.footer__container {
  padding-left: 20px;
  padding-right: 20px;
}

.footer__title{
 width: 238px;
 gap: 16px;
 display: flex;
 flex-direction: column;
}

.footer {
  padding-top: 40px;
  background-color: #fff5f6;
  position: relative;

  &__first-title {
    font-family: $secondFamily;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $thirdColor;
    
  }

    &__second-title {
    font-family: $secondFamily;
    font-size: 34px;
    line-height: 39px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: #d41443;
    background-color: #fff5f6;;
    transform: translatey(0px);
    animation: float 6s ease-in-out infinite;
     
     }

  &__social-list {
    display: flex;
    gap: 30px;
    list-style: none;
  }
  &__section {
    display: flex;
    flex-direction: column;
    gap: 40px;
    margin-bottom: 0px;
    justify-content: space-between;
  }
  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 40px;
  }
  &__contact {
    font-weight: 700;
    font-size: 16px;
    line-height: 186%;
    letter-spacing: 0.02em;
    color: #000000;
  }
  &__tel {
    color: #d41443;
    text-decoration: none;
  }
  &::after {
    content: '';
    position: absolute;
    display: block;
    left: 0;
    bottom: 76px;
    width: 100%;
    height: 1px;
    border-radius: 1px;
    background-color: #e1e1e1;
  }
  &__copyright-text {
    display: flex;
    padding-bottom: 5px;
    padding-top: 56px;
    justify-content: center;
    font-weight: 700;
    font-size: 14px;
    line-height: 186%;
    letter-spacing: 0.02em;
    color: #907e82;
  }
  &__copyright-container {
    display: flex;
    justify-content: center;
  }
}

//Tablet
 
@media screen and (min-width: 768px) {
  .footer::after {
    bottom: 60px;
  }

  .footer__section {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .footer__signature-title {
    width: 340px;
    margin-bottom: 0;
  }

  .footer__first-title {
    font-size: 26px;
    line-height: 30px;
    }

   .footer__second-title {
      font-size: 48px;
      line-height: 55px;
      letter-spacing: 0.06em;
}
}

//Desktop

@media screen and (min-width: 1280px) {
  
 .footer {
  padding-top: 120px;
 }
  .footer__title {
    width: 478px;
  }
  .footer__first-title  {
  font-family: $secondFamily;
  font-size: 30px;
  line-height: 34px;
  letter-spacing: 0.04em;
   }

   .footer__second-title {
    font-size: 58px;
    line-height: 66px;
    letter-spacing: 0.06em;}
  
  .footer__container {
    padding-left: 16px;
    padding-right: 16px;
    width: 1280px;
    margin: 0 auto;
  }

  .footer__copyright {
    padding-top: 7px;
    width: 150px;
  }

.footer__copyyright-text {
  padding-top: 67px;
}

.footer::after {
  bottom: 60px;
}
}